import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';
import { Row, Card, Form, Input, message } from 'antd';
// import BtnAddNew from 'components/common/button/BtnAddNew';
// import YearSemesterInput from 'components/common/input/YearSemesterInput';
// import { getCodeStudent, getBonusType, getBonusTypeChild } from 'services/AddReward';
import DatePickerString from 'components/common/input/DatePickerString';

import { createDocOut, getDocOutId, updateDocOut } from 'services/docOut';

// const { Option } = Select;
const handleInitialValue = initialValue => {
  const initialValueDefault = {
    code: '',
    name: '',
    birthdate: '',
    className: '',
    regNum: '',
    decDay: '',
    rewardLevel: '',
    form: '',
    typeOfReward: '',
  };
  return { ...initialValueDefault, ...initialValue };
};

@Form.create({ name: 'formTeacher' })
class FormAddReward extends Component {
  state = {
    teacherId: false,
    initialValue: {},
  };
  componentDidMount = () => {};

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ teacherId: match.params.id });
      this.getDataById(match.params.id);
    }
  };
  getDataById = async id => {
    const res = await getDocOutId(id);
    const initialValue = handleInitialValue(res.data);
    this.setState({ initialValue });
  };

  handleSubmit = e => {
    const { match } = this.props;
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (match.params.id) {
          updateDocOut(values)
            .then(() => {
              message.success('Chỉnh sửa hồ sơ đi thành công');
              this.props.history.push('/docout/list');
            })
            .catch(error => {
              console.log('error', error);
              // message.error(error.response.data.message);
            });
        } else {
          createDocOut(values)
            .then(() => {
              message.success('Thêm hệ đào tạo thành công');
              this.props.history.push('/docout/list');
            })
            .catch(error => {
              console.log('res', error.response);
              // message.error(error.response.data.message);
            });
        }
      }
    });
  };

  render() {
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { initialValue, teacherId } = this.state;
    return (
      <Card title={`${teacherId ? 'Cập nhật' : 'Thêm'} khu vực`}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item hasFeedback label="Số công văn">
              {getFieldDecorator('id', {
                initialValue: initialValue.id,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Ngày đi ">
              {getFieldDecorator('docOutDate', {
                initialValue: initialValue.docOutDate,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
            <Form.Item label="Đơn vị gởi">
              {getFieldDecorator('sentUnit', {
                initialValue: initialValue.sentUnit,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Ngày vào sổ">
              {getFieldDecorator('noteDate', {
                initialValue: initialValue.noteDate,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<DatePickerString placeholder="Chọn ngày" />)}
            </Form.Item>
            <Form.Item label="Nội dung">
              {getFieldDecorator('docContent', {
                initialValue: initialValue.docContent,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Nơi nhận">
              {getFieldDecorator('recipientPlace', {
                initialValue: initialValue.recipientPlace,
              })(<Input />)}
            </Form.Item>
            <Form.Item label="Nơi lưu">
              {getFieldDecorator('savedPlace', {
                initialValue: initialValue.savedPlace,
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel href="#department-training/list" />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormAddReward {...props} />
  </Page>
));



